/*global styles*/
/*primary styles*/
* {
  outline: none;
  font-family: Roboto;
}
#notSupportedBrowserSpan {
  display: none;
}
body {
  color: #707070;
  font-family: Roboto;
}
footer {
  position: absolute;
  display: flex;
  flex-direction: column;
  width: 100%;
  left: 0;
  padding: 10px 0 30px 0;
  color: white;
  background-color: #707070;
}
footer #SocialMediaContainer {
  font-size: 40px;
  justify-content: space-between;
  padding: 40px 10px 30px 10px;
  width: 600px;
}
footer #SocialMediaContainer > i {
  transition: all 0.3s;
  padding: 30px;
  border-radius: 5px;
  background-color: transparent;
  cursor: pointer;
}
footer #SocialMediaContainer > i:not(:hover) {
  color: white !important;
}
footer #SocialMediaContainer > i:hover {
  background-color: white;
  transform: scale(1.3);
}
footer .main-footer {
  display: flex;
  max-width: 800px;
  margin-left: 10%;
  justify-content: space-between;
}
footer .main-footer > div {
  padding-right: 30px;
}
footer .main-footer h3 {
  font-size: 20px;
  font-weight: bold;
  margin: 0;
}
footer .main-footer .footer-link {
  color: white;
  text-decoration: none;
}
footer .main-footer .footer-link:hover {
  color: white;
}
footer .main-footer .contact-link-box {
  display: flex;
  flex-direction: column;
}
footer .main-footer div {
  display: flex;
  flex-direction: column;
}
.fa-google {
  content: url("../assets/google.svg");
}
.q-page {
  display: flex;
  justify-content: center;
}
.q-page > div {
  width: 90%;
  padding: 0;
  max-width: 1000px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  word-wrap: break-word;
  font-family: Raleway;
}
.tooltip {
  left: unset !important;
  right: 2%;
  top: 70px !important;
}
a {
  color: #707070;
  cursor: pointer;
}
a:hover {
  color: #D56026;
}
.text-white {
  color: white !important;
}
.q-editor__toolbar {
  flex-wrap: wrap !important;
}
.q-tooltip {
  font-size: 14px;
}
font-awesome-icon {
  margin: auto;
}
h2 {
  line-height: normal;
}
.sticky {
  position: -webkit-sticky;
  /* Safari */
  position: sticky;
  top: 0px;
  z-index: 100;
  background: white;
  transform: translate3d(0, 80px, 0);
  transition: transform 0.1s ease-out, height 1s;
}
.sticky.at-top {
  transform: translate3d(0, 0, 0);
}
/*Header*/
#Header {
  display: flex;
  justify-content: space-between;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  height: 80px;
  left: 0;
  width: 100%;
  top: 0;
  background-color: white;
  z-index: 1000;
  flex-direction: column;
  position: fixed;
  transform: translate3d(0, 0, 0);
  transition: transform 0.1s ease-out, height 1s;
  pointer-events: all;
}
#Header.header-hidden {
  box-shadow: none;
  transform: translate3d(0, -100%, 0);
}
#Header .top-row {
  display: flex;
  justify-content: space-between;
}
#Header .top-row div {
  width: 100%;
}
#Header .top-row .header-page-link {
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  justify-content: center;
  transition: 0.3s;
  text-decoration: none;
  position: relative;
  margin: 10px 0;
  padding: 1px 10px;
  color: #707070;
}
#Header .top-row .header-page-link:hover {
  color: #D56026;
}
#Header .top-row .header-page-link::after {
  content: "";
  background: #D56026;
  height: 1px;
  position: absolute;
  bottom: 0;
  transition: 0.16s all 0.025s;
}
#Header .top-row .header-page-link::after {
  right: 100%;
  left: 0;
}
#Header .top-row .header-page-link:hover ~ #Header .top-row .header-page-link::after {
  right: 0;
  left: 100%;
}
#Header .top-row .header-page-link:hover::after {
  left: 0;
  right: 0;
}
#Header .top-row .center-col {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  max-height: 80px;
}
#Header .top-row .center-col img {
  height: 60px;
}
#Header .top-row .left-col {
  padding-left: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
  max-height: 80px;
}
#Header .top-row .left-col img {
  height: 75px;
}
#Header .top-row .left-col a {
  max-width: 150px;
}
#Header .top-row .left-col .beta-span {
  position: absolute;
  top: 2px;
  left: 155px;
}
#Header .top-row .right-col {
  display: flex;
  justify-content: space-around;
  padding-right: 20px;
  max-height: 80px;
}
#Header .top-row .right-col #BurgerMenu {
  width: auto;
  display: none;
}
#Header .top-row .right-col #BurgerMenu .ham {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  transition: transform 400ms;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
#Header .top-row .right-col #BurgerMenu .hamRotate.active {
  transform: rotate(45deg);
}
#Header .top-row .right-col #BurgerMenu .hamRotate180.active {
  transform: rotate(180deg);
}
#Header .top-row .right-col #BurgerMenu .line {
  fill: none;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  stroke: #D56026;
  stroke-width: 2px;
  stroke-linecap: round;
}
#Header .top-row .right-col #BurgerMenu .ham7 .top {
  stroke-dasharray: 40 82;
}
#Header .top-row .right-col #BurgerMenu .ham7 .middle {
  stroke-dasharray: 40 111;
}
#Header .top-row .right-col #BurgerMenu .ham7 .bottom {
  stroke-dasharray: 40 161;
}
#Header .top-row .right-col #BurgerMenu .ham7.active .top {
  stroke-dasharray: 17 82;
  stroke-dashoffset: -62px;
}
#Header .top-row .right-col #BurgerMenu .ham7.active .middle {
  stroke-dashoffset: 23px;
}
#Header .top-row .right-col #BurgerMenu .ham7.active .bottom {
  stroke-dashoffset: -83px;
}
#Header .bottom-row {
  height: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: right;
  overflow: hidden;
  padding-right: 30px;
}
#Header .bottom-row .mobile-header-page-link {
  display: flex;
  opacity: 0;
  text-transform: uppercase;
  flex-direction: column;
  justify-content: center;
  padding: 10px;
  color: #707070;
  transition: 1s;
  text-decoration: none;
}
#Header .bottom-row .mobile-header-page-link:hover {
  color: #D56026;
}
#Header.active {
  height: 250px;
}
#Header.active .bottom-row {
  height: 170px;
}
#Header.active .bottom-row .mobile-header-page-link {
  opacity: 1;
}
#Header .router-link-exact-active {
  color: #D56026 !important;
}
.q-page-container {
  min-height: 90vh;
  margin-top: 80px;
  padding-left: 0 !important;
}
.q-avatar {
  cursor: pointer;
  padding: 8px 10px;
}
.q-avatar a {
  white-space: nowrap;
}
.uploader {
  width: 150px;
  height: 150px;
  border: solid #d5d5d5 1px;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url('../statics/dummy-image-landscape-1-150x150.jpg');
  background-position: center;
  background-size: cover;
}
.q-input,
.q-select,
.q-textarea {
  max-width: 300px;
}
.cursor-DandD {
  cursor: move;
  /* fallback if grab cursor is unsupported */
  cursor: grab;
  cursor: -moz-grab;
  cursor: -webkit-grab;
}
.cursor-DandD:active {
  cursor: grabbing;
  cursor: -moz-grabbing;
  cursor: -webkit-grabbing;
}
.box-seperator {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  position: absolute;
  left: 0;
  width: 100%;
  background-color: white;
  display: flex;
  justify-content: center;
  text-align: center;
  padding: 0 5px 0 5px;
}
.box-seperator h1 {
  font-size: 2.14285714em;
  margin: 0;
}
.box-seperator-placholder {
  height: 110px;
}
.preloadOverlay {
  background-color: white;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 9800;
  top: 0;
}
.q-loading {
  background-color: white !important;
  color: white !important;
}
.q-inner-loading {
  z-index: 10;
}
.pagination button {
  background: #ffffffbf;
  margin-left: 5px;
}
.register-btn {
  position: fixed;
  top: 82vh;
  right: 10px;
  z-index: 1000;
  box-shadow: 0 0 0 rgba(211, 104, 48, 0.4);
  animation: pulse 2s infinite;
}
.add-country-container {
  height: auto;
  padding-bottom: 15px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-left: 5px;
}
.q-avatar .q-icon.notranslate:not(.material-icons) {
  display: none;
}
.notification-card {
  margin: 20px;
}
.notification-card .message {
  font-size: 1.28571429em;
}
.notification-card .created-at {
  font-size: 12px;
  text-align: right;
}
.notification-card .tag {
  font-size: 14px;
}
.raleway {
  font-family: Raleway;
}
.font-small {
  font-size: 0.7em;
}
.font-medium {
  font-size: 1.28571429em;
}
.font-large {
  font-size: 2.14285714em;
}
.font-very-large {
  font-size: 3.57142857em;
}
.q-chip__content {
  text-overflow: ellipsis;
  display: block;
  white-space: nowrap;
  overflow: hidden;
}
@media only screen and (min-height: 0px) and (max-height: 665px) {
  .scroll-down-icon {
    display: none;
  }
}
@media only screen and (min-width: 720px) and (max-width: 959px), only screen and (min-width: 0px) and (max-width: 719px) {
  .v-step {
    max-width: 40% !important;
  }
  .right-col {
    text-align: left;
    align-items: flex-end;
    justify-content: flex-end !important;
  }
  .right-col .header-page-link {
    margin: 0;
  }
  .right-col .q-avatar {
    margin: auto;
    margin-right: 0;
    padding: 5px;
  }
  .box-seperator h1 {
    font-size: 20px !important;
    line-height: 3;
  }
  .box-seperator-placholder {
    height: 130px;
  }
  .section-img-placeholder > h2 {
    font-size: 2.14285714em;
  }
  #BurgerMenu {
    display: block !important;
  }
  .header-page-link,
  .register-header-link {
    display: none !important;
  }
  .main-footer {
    flex-direction: column;
    text-align: center;
    align-items: center;
  }
  .main-footer .left-footer-col {
    width: auto !important;
    padding: 0 !important;
  }
  .main-footer .right-footer-col {
    flex-direction: column;
    text-align: center;
    align-items: center;
  }
}
@media only screen and (min-width: 0px) and (max-width: 719px) {
  .center-col img {
    display: none;
  }
  h1 {
    font-size: 3.57142857em !important;
  }
  h2,
  h3,
  h4,
  h5 {
    font-size: 2.14285714em;
  }
}
@-webkit-keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(211, 104, 48, 0.5);
  }
  70% {
    -webkit-box-shadow: 0 0 0 15px rgba(211, 104, 48, 0);
  }
  100% {
    -webkit-box-shadow: 0 0 0 0 rgba(211, 104, 48, 0);
  }
}
@keyframes pulse {
  0% {
    -moz-box-shadow: 0 0 0 0 rgba(211, 104, 48, 0.5);
    box-shadow: 0 0 0 0 rgba(211, 104, 48, 0.5);
  }
  70% {
    -moz-box-shadow: 0 0 0 15px rgba(211, 104, 48, 0);
    box-shadow: 0 0 0 15px rgba(211, 104, 48, 0);
  }
  100% {
    -moz-box-shadow: 0 0 0 0 rgba(211, 104, 48, 0);
    box-shadow: 0 0 0 0 rgba(211, 104, 48, 0);
  }
}
