.ext-swipe-to-close {
  transition: all 300ms;
  position: relative;
}
.ext-swipe-to-close > div:not(.ext-swipe-to-close__swipe-disable-line) {
  height: inherit;
  min-height: fit-content;
}
.ext-swipe-to-close.--swiping {
  transition: all 0ms;
}
.ext-swipe-to-close__swipe-disable-line {
  width: 100%;
  position: absolute;
  height: 1px;
  background: transparent;
  top: -15px;
}
