















.ext-swipe-to-close
  transition all 300ms
  position relative
  > div:not(.ext-swipe-to-close__swipe-disable-line)
    height inherit
    min-height fit-content
  &.--swiping
    transition all 0ms
  &__swipe-disable-line
    width 100%
    position absolute
    height 1px
    background transparent
    top -15px
.q-dialog__inner--maximized
  .ext-swipe-to-close
    &__swipe-disable-line
      top 1px !important

